import Vector from './utils/vector';
import Engine from './utils/engine';
// import Engine from './utils/engine3d';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const columns = 50;
const rows = 50;

const app = new Engine(el, {
  debug: false,
  clickToggleDebug: true,
  width: WIDTH,
  height: HEIGHT
});

const points = [
  new Vector({ x: 0, y: 0 })
];

function calculateGrid(_columns, _rows, _width, _height) {
  return {
    sizes: {
      x: _width / _columns,
      y: _height / _rows
    }
  };
}

function drawGrid(_columns, _rows, _width, _height) {
  let xSize = _width / _columns;
  let ySize = _height / _rows;

  for (let x = 0; x < _columns; x++) {
    for (let y = 0; y < _rows; y++) {
      ctx.strokeRect(0, 0, x * xSize, y * ySize);
    }
  }
}

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);
  if (debug) {
    drawGrid(columns, rows, width, height);
  }

  if (frame % 5 == 0) {
    addPoint();
  }

  const { sizes } = calculateGrid(columns, rows, width, height);


  for (let i = 0; i < points.length; i++) {
    ctx.save();
    ctx.fillStyle = 'rgba(0, 0, 0, 0.25)';
    ctx.translate(points[i].x * sizes.x, points[i].y * sizes.y);

    switch (points[i].dir) {
      case 'TOP':
        ctx.fillRect(0, sizes.y / 2, sizes.x, sizes.y / 2);
        break;
      case 'RIGHT':
        ctx.fillRect(0, 0, sizes.x / 2, sizes.y);
        break;
      case 'BOTTOM':
        ctx.fillRect(0, 0, sizes.x, sizes.y / 2);
        break;
      case 'LEFT':
        ctx.fillRect(sizes.x / 2, 0, sizes.x / 2, sizes.y);
        break;
    }
    ctx.restore();
  }
});

const DIRS = {
  RIGHT: { x: 1, y: 0 },
  BOTTOM: { x: 0, y: 1 },
  LEFT: { x: -1, y: 0 },
  TOP: { x: 0, y: -1 },
};

function getPossiblePoint(exclude = []) {
  let last = points.length ? points[points.length - 1] : false;

  let dirKeys = Object.keys(DIRS).filter(k => exclude.indexOf(k) < 0);

  let randDirection = dirKeys[Math.floor(Math.random() * dirKeys.length)];
  let point = last.clone().add(DIRS[randDirection]);

  if (isLastPoint(point) || isOutOfBounds(point)) {
    exclude.push(randDirection);
    return getPossiblePoint(exclude);
  } else {
    point.dir = randDirection;
    return point;
  }
}

function isLastPoint(point) {
  let last = points.length > 1 ? points[points.length - 2] : points[points.length - 1] ;
  return last.x === point.x && last.y === point.y;
}

function isOutOfBounds(point) {
  return point.x < 0 || point.y < 0 || point.x >= (columns - 1) || point.y >= (rows - 1);
}

function addPoint() {
  points.push(getPossiblePoint());
}

// el.addEventListener('click', e => {
//   addPoint();
// });

app.start();
